.hidden {
  display: none;
}

.loader {
  width: 96px;
  box-sizing: content-box;
  height: 48px;
  background: #fff;
  border: 2px solid #080200;
  border-bottom-width: 50px;
  border-radius: 100%;
  animation: yinYang 3s linear infinite;
  position: relative;
}

.loader:before {
  content: "";
  width: 12px;
  height: 12px;
  box-sizing: content-box;
  background: #fff;
  border: 18px solid #070200;
  border-radius: 100%;
  position: absolute;
  top: 50%;
  left: 0;
}

.loader:after {
  content: "";
  width: 12px;
  height: 12px;
  box-sizing: content-box;
  background: #030100;
  border: 18px solid #fff;
  border-radius: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
}

@keyframes yinYang {
  100% {
    transform: rotate(360deg);
  }
}

.wraper {
  width: 300px;
  margin-bottom: 20px;
}

.cat-info {
  display: flex;
}

.infoCat {
  margin-left: 10px;
}

.breed, .temperament {
  margin-top: 0;
  margin-bottom: 0;
}

